@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap"); /* get fonts from google */
/* https://fonts.google.com/ */

* {
  margin: 0;
  font-family: "Raleway";
  color: #aa01019c;
}
.navbar {
  padding: 20px;
  /* odległość między elementami wewnątrz w każdą stronę */
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #979797b9;
}
.navbar h1 {
  color: #7e12129c;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #1b04049c;
}
/* zmiana koloru jeśli najedziemy */

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
.myApp-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.myApp-preview:hover {
  box-shadow: 1px 3px 5px #7e12129c;
}
.myApp-preview h2 {
  font-size: 20px;
  color: #7e12129c;
  margin-bottom: 8px;
}
.myApp-preview a {
  text-decoration: none;
}
.app-details h2 {
  font-size: 20px;
  color: #7e1212df;
  margin-bottom: 10px;
}
.app-details div {
  margin: 20px 0;
  color: #7e1212df;
}
.app-details button {
  background: #7e12129c;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #7e1212df;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #7e1212df;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
